<template>
    <div class="left-delete">
        <div class="move"
             @touchstart="_touchStart"
             @touchmove="_touchMove"
             @touchend="_touchEnd"
             :style="txtStyle">
            <slot></slot>
        </div>
        <div class="deleteIcon" :style="zIndex"  @click.prevent="deleteItem(index)"><img src="../images/video_icon_report.png" width="18px" style="float: left;margin-top: 17.5px;margin-right: 5px;" alt=""></div>
    </div>
</template>

<script>
    export default {
        props: {
            index: Number
        },
        data() {
            return {
                startX: 0,       //
                  moveX: 0,       //
                  disX: 0,       //
                  txtStyle: '',
                delWidth: 583,
                top: '',
                zIndex: 'right:-70px;',
            }
        },
        methods: {
            _touchStart: function(ev) {
                ev = ev || event;
                if(ev.touches.length == 1){
                    // 
                    this.startX = ev.touches[0].clientX;
                    // console.log(this.startX)
                }
                ev.preventDefault();
                 ev.stopPropagation();
            },
            _touchMove: function(ev) {
                ev = ev || event;
                if(ev.touches.length == 1) {
                    // 
                    this.moveX = ev.touches[0].clientX
                    // 
                    this.disX = this.startX - this.moveX;
                    // console.log('disX==>',this.disX)
                    // 
                    if(this.disX < 0 || this.disX == 0) {
                        // console.log('');
                        this.txtStyle = "transform:translateX(0rem)";
                        this.zIndex = "right:-70px;";
                    }else if (this.disX > 0) {
                    //-leftdelWidth delWidth
                        // this.txtStyle = "transform:translateX(-" + this.disX/100 + "rem)";
                        if (this.disX >= this.delWidth/100) {
                            this.txtStyle = "transform:translateX(-" + this.delWidth/100 + "rem)";
                            this.zIndex = "right:0;";
                        }
                    }
                }
                ev.preventDefault();
                ev.stopPropagation();
            },
            _touchEnd: function(ev) {
                if (event.changedTouches.length == 1) {
                    this.startX = 0;
                    // this.zIndex = "right:-70px;";
                    // console.log(event.changedTouches[0].clientX)
                    // 
                    let endX = event.changedTouches[0].clientX;
                    // ,
                    this.disX = this.startX - endX;
                    //1/2

                }
                ev.preventDefault();
                ev.stopPropagation();
            },
            deleteItem: function(index) {
                // console.log(index)
                this.txtStyle = "transform:translateX(0rem)";
                this.zIndex = "right:-70px;";
                this.$emit('deleteItem', index);
                // window.setupWebViewJavascriptBridge(function(bridge) {
                //     let _lan = (navigator.browserLanguage || navigator.language).toLowerCase();
                //      if(_lan === 'zh-cn') {
                //         bridge.callHandler('makeToast', '');
                //      }else {
                //         bridge.callHandler('makeToast', '');
                //      }
                // })
            }
        }
    }
</script>

<style>
    .left-delete{
        width:100%;
        height:100%;
        position:relative;
        overflow: hidden;
        box-sizing: border-box;
        padding: 12px 12px;
    }
    .move{
        /* position: relative; */
        overflow: hidden;
        transition: all 0.3s;
    }
    .deleteIcon{
        width: 50px;
        padding-left: 12px; 
        padding-right: 8px;
        background: #ff2955;
        position: absolute;
        color: #fff;
        height: 55px;
        line-height: 55px;
        overflow: hidden;
        top:0;
        transition: all 0.3s;
    }
</style>


