                                    <p><span style="text-align: left;margin-left: 10px;"><em>0</em></span><i style="text-align: left;margin-left: -5px;">{{activity.works}}</i></p>
                                    <p><span><img src="../../images/timeline_icon_coins.png"><em>0</em></span><i>{{activity.Gcoin}}</i></p>
