<script>
    import http from '@env/http.js';
    require('../../utils/common.js')
    export default {
        data() {
            return {
                ranking: [],
                me: {},
                idx: 0,
                idx2: 0,
                loadingShow: false,
                activity: {
                    theme:' cosplay',
                    time: '',
                    start: '',
                    end: '',
                    rewardList: '',
                    works: '',
                    Gcoin: '',
                    like: 'Like',
                    videos: '',
                    noRanking: '',
                    award: '',
                    me: '',
                    all: '',
                    more: '',
                    rule: {
                        title: '',
                        p1: '1.<i style="color: #fc4c86;">cosplay</i>',
                        p2: '2.cosplay',
                        p3: '3.1OK',
                        p4: '4.Like'
                    },
                    rewards: [{
                        p1: '20000',
                        p2: '',
                        p3: '2',
                        p4: '',
                        p5: 'Twitterweibo1',
                        p6: 'weiboweibobilibili',
                        p7: 'Groupy'
                    }, {
                        p1: '10000',
                        p2: '1',
                        p3: '',
                        p4: 'Twitterweibo2',
                        p5: 'weibobilibili'
                    }, {
                        p1: '5000',
                        p2: '1',
                        p3: '',
                        p4: 'Twitterweibo3',
                        p5: 'weibobilibili'
                    }]
                }
            }
        },
        methods: {
          getList(token) {
            let self = this;
            let _lan = (navigator.browserLanguage || navigator.language).toLowerCase();
            if(self.idx < 2) {
                if(token) {
                    http.defaults.headers.common['Authorization'] = 'Token '+token;
                }else {
                    http.defaults.headers.common['Authorization'] = 'Token '+self.$route.query.token;
                }
                http.get('/video/activityIdols',{
                    params: {
                        activityId:1000,
                        rows: 10
                    }
                }).then(function(res){
                    self.ranking = res.data.ranking;
                    self.me = res.data.self;
                    self.loadingShow = true;
                    console.log(self.ranking);
                    console.log(self.me);
                }).catch(function(){
                    self.idx++;
                    window.setupWebViewJavascriptBridge(function(bridge) {
                        bridge.callHandler('getToken', {'targetType':'0','targetId':'0'}, function responseCallback(responseData) {
                            self.getList(responseData.token);
                        })
                    })
                });
            }else {
                 window.setupWebViewJavascriptBridge(function(bridge) {
                    if(_lan === 'zh-cn') {
                        bridge.callHandler('makeToast', '');
                     }else {
                        bridge.callHandler('makeToast', '\\n');
                     }
                })
            }
          }
        },
        mounted() {
        },
        created() {
             let _lan = (navigator.browserLanguage || navigator.language).toLowerCase();
             if(_lan === 'zh-cn') {
                 this.activity= {
                    theme:' ##',
                    time: '',
                    start: '',
                    end: '',
                    rewardList: ' ',
                    works: '',
                    Gcoin: 'G',
                    like: 'Like',
                    videos: '',
                    noRanking: '',
                    award: '',
                    me: ' ',
                    all: '',
                    more: '',
                    rule: {
                        title: '',
                        p1: '1.# ',
                        p2: '2.',
                        p3: '3.',
                        p4: '4.'
                    },
                    rewards: [{
                        p1: '20000',
                        p2: '',
                        p3: '2',
                        p4: 'Banner',
                        p5: 'Twitterweibo',
                        p6: 'bilibili',
                        p7: 'Groupy'
                    }, {
                        p1: '10000',
                        p2: '1',
                        p3: 'banner',
                        p4: 'Twitter',
                        p5: 'bilibili'
                    }, {
                        p1: '5000',
                        p2: '1',
                        p3: 'banner',
                        p4: 'Twitter',
                        p5: 'bilibili'
                    }]
                }
              } else {
                this.activity= {
                    theme:' cosplay',
                    time: '',
                    start: '',
                    end: '',
                    rewardList: '',
                    works: '',
                    Gcoin: '',
                    like: 'Like',
                    videos: '',
                    noRanking: '',
                    award: '',
                    me: '',
                    all: '',
                    more: '',
                    rule: {
                        title: '',
                        p1: '1.<i style="color: #fc4c86;">cosplay</i>',
                        p2: '2.cosplay',
                        p3: '3.1OK',
                        p4: '4.Like'
                    },
                    rewards: [{
                        p1: '20000',
                        p2: '',
                        p3: '2',
                        p4: '',
                        p5: 'Twitterweibo1',
                        p6: 'weiboweibobilibili',
                        p7: 'Groupy'
                    }, {
                        p1: '10000',
                        p2: '1',
                        p3: '',
                        p4: 'Twitterweibo2',
                        p5: 'weibobilibili'
                    }, {
                        p1: '5000',
                        p2: '1',
                        p3: '',
                        p4: 'Twitterweibo3',
                        p5: 'weibobilibili'
                    }]
                }
              }
            this.getList();
        }
    }
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
    @font-face {
      font-family: 'Helvetica Condensed Bold Italic';
      src: url('../../font/Helvetica Condensed Bold Italic.eot');
      src: url('../../font/Helvetica Condensed Bold Italic.woff2') format('woff2'),
