    }
    .activity_ranking {
        font-size: 14px;
        >p {
            padding-left: 12px;
            padding-top: 12px;
            color: #999;
        }
        .ranking_list {
            padding: 12px 12px 0;
            text-align: center;
            h5 {
                color: #999;
                text-align: left;
            }
            li {
                text-align: left;
                overflow: hidden;
                border-bottom: 1px solid #eee;
                padding: 10px 0;
                >* {
                    float: left;
                }
                >span {
                    font-family: 'Helvetica Condensed Bold Italic';
                    font-size: 24px;
                    margin-top: 15px;
                    display: inline-block;
                    width: 28px;
                    // text-align: center;
                }
                >span.first {
                    color: #F3B714;
                }
                >span.second {
                    color: #888;
                }
                >span.three {
                    color: #FF8500;
                }
                >.img_content {
                    width: 50px;
                    position: relative;
                    padding-top: 9px;
                    padding-left: 4px;
                    margin-right: calc(100vw * 0.04);
                    img.avatar {
                        width: 50px;
                        height: 50px;
                        border-radius: 50%;
                        border: 1px solid #888;
                    }
                    img.crown {
                        position: absolute;
                        left: 0;
