                        font-size: 16px;
                        font-weight: 600;
                        color: #666;
                    }
                    .idol_desc {
                        display: flex;
                        p {
                            flex: 1;
                            text-align: left;
                            >span {
                                display: block;
                                text-align: center;
                                img {
                                    width: 12px;
                                    vertical-align: middle;
                                    margin-right: 5.5px;
                                }
                                em {
                                    display: inline-block;
                                    vertical-align: middle;
                                    font-size: 14px;
                                    font-weight: 600;
                                    color: #333;
                                    max-width: calc(100% - 18px);
                                    overflow: hidden;
                                    text-overflow:ellipsis;
