                font-size: 14px;
                display: inline-block;
                padding: 12px 0 0;
            }
        }
    }
    .activity_reward {
        li {
            overflow: hidden;
            background: #F9EDEF;
            border: 1px solid #F5D9DE;
            border-radius: 2px;
            margin: 12px 12px 0;
            padding: 12px;
            >img {
                vertical-align: middle;
