<template>
    <div class="main">
        <div class="header">
            <div class="income eBorder">
                <p>
                    <span class="detail_title">{{fans_text.mine}} </span>
                    <span class="detail_gcoin"><img src="../../images/icon_fans .png" alt="" class="icon"><i class="video_money left" :class="{'left_show':gcoinList.fansCount || gcoinList.fansCount == 0}">{{gcoinList.fansCount?Number(gcoinList.fansCount).toLocaleString():0}}</i></span>
                </p>
                <p>
                    <span class="detail_title">{{fans_text.add}}</span>
                    <span class="detail_gcoin"><img src="../../images/icon_fans .png" alt="" class="icon"><i class="video_money left" :class="{'left_show':gcoinList.fansCount || gcoinList.fansCount == 0}">{{gcoinList.fansIncreased?Number(gcoinList.fansIncreased).toLocaleString():0}}</i></span>
                </p>
            </div>
            <div class="detailPages">
                <a class="tabs active" @click="changePages(0)">{{fans_text.Gcoin}}</a>
                <a class="tabs" @click="changePages(1)">{{fans_text.like}}</a>
                <a class="tabs" @click="changePages(2)">{{fans_text.time}}</a>
            </div>
        </div>
        <div class="content">
          <swiper :options="swiperOption" ref="mySwiper" class="banner_container">
            <!-- slides -->
            <swiper-slide id="swiper1">
                <ul class="comment_list">
                    <li v-for="(gFans,key) in gcoinList.fansList" style="padding: 0;">
                        <left-slider :index="key" @deleteItem="deleteItem(gFans.fansId)">
                            <span>
                                <img src="../../images/icon_metal_1.png" alt="" v-if="key==0">
                                <img src="../../images/icon_metal_2.png" alt="" v-if="key==1">
                                <img src="../../images/icon_metal_3.png" alt="" v-if="key==2">
                                <i class="rank_num" v-if="key>2">{{key+1}}</i>
                            </span>
                            <img class="avatar" :src="gFans.fans?(gFans.fans.avatar?gFans.fans.avatar:'http://h5.groupy.vip/static/images/default_img.png'):'http://h5.groupy.vip/static/images/default_img.png'" onerror="this.src='http://h5.groupy.vip/static/images/default_img.png'" alt="">
                            <span>{{gFans.fans?(gFans.fans.nickname?gFans.fans.nickname:'...'):'...'}}</span>
                            <!-- <img :src="gFans.fans?'/static/images/icon_level_'+(gFans.fans.levelPlatform)+'.png':'http://h5.groupy.vip/static/images/icon_level_0.png'" onerror="this.src='http://h5.groupy.vip/static/images/icon_level_0.png'" class="level" alt=""> -->
                            <span class="level">Lv.{{gFans.fans?(gFans.fans.levelPlatform?gFans.fans.levelPlatform:0):0}}</span>
                            <img class="fans_medal" :src="'/static/images/icon_medal_'+(gFans.fans.medal)+'.png'" v-if="gFans.fans.medal&&gFans.fans.medal>0" alt="">
                            <!-- <img :src="gFans.fans?'/static/images/icon_level_'+(gFans.fans.medal)+'.png':''" class="level" alt=""> -->
                            <i>
                                <img src="../../images/timeline_icon_coins.png" alt="">{{Number(gFans.expendGprice?gFans.expendGprice:'0').toLocaleString()}}
                            </i>
                        </left-slider>
                    </li>
                </ul>
                <div class="default_page" v-show="gcoinList.fansList?gcoinList.fansList.length == 0:false">
                    <img src="../../images/default_no coin.png" alt="">
                    <p>{{fans_text.noneGcoin}}</p>
                </div>
            </swiper-slide>
            <swiper-slide id="swiper2">
                <ul class="comment_list">
                    <li v-for="(popularity,key) in popularityList.fansList" style="padding: 0;">
                        <left-slider :index="key" @deleteItem="deleteItem(popularity.fansId)">
                            <span>
                                <img src="../../images/icon_metal_1.png" alt="" v-if="key==0">
                                <img src="../../images/icon_metal_2.png" alt="" v-if="key==1">
                                <img src="../../images/icon_metal_3.png" alt="" v-if="key==2">
                                <i v-if="key>2">{{key+1}}</i>
                            </span>
                            <img class="avatar" :src="popularity.fans?(popularity.fans.avatar?popularity.fans.avatar:'http://h5.groupy.vip/static/images/default_img.png'):'http://h5.groupy.vip/static/images/default_img.png'"  onerror="this.src='http://h5.groupy.vip/static/images/default_img.png'" alt="">
                            <span>{{popularity.fans?(popularity.fans.nickname?popularity.fans.nickname:'...'):'...'}}</span>
                            <!-- <img :src="popularity.fans?'/static/images/icon_level_'+(popularity.fans.levelPlatform)+'.png':'/static/images/icon_level_0.png'" onerror="this.src='http://h5.groupy.vip/static/images/icon_level_0.png'" class="level" alt=""> -->
                            <span class="level">Lv.{{popularity.fans?(popularity.fans.levelPlatform?popularity.fans.levelPlatform:0):0}}</span>
                            <img class="fans_medal" :src="'/static/images/icon_medal_'+(popularity.fans.medal)+'.png'" v-if="popularity.fans.medal&&popularity.fans.medal>0" alt="">
                            <!-- <img :src="popularity.fans?'/static/images/icon_level_'+(popularity.fans.medal)+'.png':''" class="level" alt=""> -->
                            <i>
                                <img src="../../images/timeline_icon_likes.png" alt="">{{Number(popularity.totalNums?popularity.totalNums:'0').toLocaleString()}}
                            </i>
                        </left-slider>
                    </li>
                </ul>
                <div class="default_page" v-show="popularityList.fansList.length == 0">
                    <img src="../../images/default_no like.png" alt="">
                    <p>{{fans_text.noneLike}}</p>
                </div>
            </swiper-slide>
            <swiper-slide id="swiper3">
                <ul class="comment_list">
                    <li v-for="(fans,key) in joinList.fansList" style="padding: 0;">
                        <left-slider :index="key" @deleteItem="deleteItem(fans.fansId)">
                            <img class="avatar" :src="fans.fans?(fans.fans.avatar?fans.fans.avatar:'http://h5.groupy.vip/static/images/default_img.png'):'http://h5.groupy.vip/static/images/default_img.png'"  onerror="this.src='http://h5.groupy.vip/static/images/default_img.png'" alt="">
                            <span>{{fans.fans?(fans.fans.nickname?fans.fans.nickname:'...'):'...'}}</span>
                            <!-- <img :src="fans.fans?'/static/images/icon_level_'+(fans.fans.levelPlatform)+'.png':'http://h5.groupy.vip/static/images/icon_level_0.png'" onerror="this.src='http://h5.groupy.vip/static/images/icon_level_0.png'" class="level" alt=""> -->
                            <span class="level">Lv.{{fans.fans?(fans.fans.levelPlatform?fans.fans.levelPlatform:0):0}}</span>
                            <img class="fans_medal" :src="'/static/images/icon_medal_'+(fans.fans.medal)+'.png'" v-if="fans.fans.medal&&fans.fans.medal>0" alt="">
                            <!-- <img :src="fans.fans?'/static/images/icon_level_'+(fans.fans.medal)+'.png':''" class="level" alt=""> -->
                            <i style="width: 20%;" v-html="formatTime(fans.startdate)"></i>
                        </left-slider>
                    </li>
                </ul>
                <div class="default_page" v-show="joinList.fansList.length == 0">
                    <img src="../../images/default_no history.png" alt="">
                    <p>{{fans_text.noneComment}}</p>
                </div>
            </swiper-slide>
          </swiper>
        </div>
       <!--  <div class="bigLoading" v-show="loadingBig">
            <img src="../../images/loading_2.png" alt="">
        </div> -->
    </div>
</template>

<script>
    import LeftSlider from '../leftSlider.vue';
    import { swiper, swiperSlide } from 'vue-awesome-swiper';
    import $ from 'n-zepto';
    import http from '@env/http.js';
    require('../../utils/common.js')
    export default {
        data() {
            return {
                swiperOption: {
                  notNextTick: true,
                  // grabCursor : true,
                  setWrapperSize :true,
                  // autoHeight: true,
                  pagination : '.swiper-pagination',
                  paginationClickable :true,
                  //loop : true,
                  observeParents:true,
                  debugger: true,
                  onTransitionStart(swiper){
                    $('.tabs').removeClass('active')
                    $('.tabs').eq(swiper.activeIndex).addClass('active');
                  },
                },
                joinList: {
                    fansList: []
                },
                gcoinList: {},
                popularityList: {
                    fansList: []
                },
                // loadingBig: true,
                idx: 0,
                idx2: 0,
                idx1: 1,
                fans_text: {
                    mine: '',
                    add: '',
                    Gcoin: '',
                    like: 'Like',
                    time: '',
                    noneGcoin: '',
                    noneLike: 'Like',
                    noneComment: ''
                }
            }
        },
        components: {
            LeftSlider
        },
        methods: {
            changePages(val) {
                let tabs = $('.tabs');
                tabs.removeClass('active');
                tabs.eq(val).addClass('active')
                this.swiper.slideTo(val, 500, false)
              },
            formatTime(key) {
                  let timer = new Date(key);
                  return timer.Format('yyyy.MM.dd');
            },
            TransferString(content) {
                 let string = content;    
                 try{    
                    string=string.replace(/\r\n/g,"<br>")    
                    string=string.replace(/\n/g,"<br>");    
                 }catch(e) {    
                    console.log(e.message);    
                 }
                 return string;    
            },
            getJoin(token) {
                let self = this;
                if(self.idx1 < 2) {
                    if(token) {
                        http.defaults.headers.common['Authorization'] = 'Token '+token;
                    }else {
                        http.defaults.headers.common['Authorization'] = 'Token '+self.$route.query.token;
                    }
                    http.get('/statistic/time').then(function(res){
                        if(res.status == 200) {
                            self.joinList = res.data;
                            console.log(self.joinList);
                        }else {
                            window.setupWebViewJavascriptBridge(function(bridge) {
                                bridge.callHandler('getToken', {'targetType':'0','targetId':'0'}, function responseCallback(responseData) {
                                    self.getJoin(responseData.token);
                                })
                            })
                        }
                    }).catch(function(){
                        self.idx1++;
                        window.setupWebViewJavascriptBridge(function(bridge) {
                            bridge.callHandler('getToken', {'targetType':'0','targetId':'0'}, function responseCallback(responseData) {
                                self.getGoin(responseData.token);
                            })
                        })
                    });
                }
                    
            },
            getGcoin(token) {
                let self = this;
                if(self.idx < 2) {
                    if(token) {
                        http.defaults.headers.common['Authorization'] = 'Token '+token;
                    }else {
                        http.defaults.headers.common['Authorization'] = 'Token '+self.$route.query.token;
                    }
                    http.get('/statistic/gb').then(function(res){
                        // self.loadingBig = false;
                        if(res.status == 200) {
                            self.gcoinList = res.data;
                            console.log(self.gcoinList)
                        }else {
                            window.setupWebViewJavascriptBridge(function(bridge) {
                                bridge.callHandler('getToken', {'targetType':'0','targetId':'0'}, function responseCallback(responseData) {
                                    self.getGoin(responseData.token);
                                })
                            })
                        }
                    }).catch(function(){
                        self.idx++;
                        window.setupWebViewJavascriptBridge(function(bridge) {
                            bridge.callHandler('getToken', {'targetType':'0','targetId':'0'}, function responseCallback(responseData) {
                                self.getGoin(responseData.token);
                            })
                        })
                    });
                } else {
                    // self.loadingBig = false;
                    window.setupWebViewJavascriptBridge(function(bridge) {
                        let _lan = (navigator.browserLanguage || navigator.language).toLowerCase();
                         if(_lan === 'zh-cn') {
                            bridge.callHandler('makeToast', '');
                         }else {
                            bridge.callHandler('makeToast', '\\n');
                         }
                    })
                }
            },
            getPopularity(token) {
                let self = this;
                if(self.idx2 < 2) {
                    if(token) {
                        http.defaults.headers.common['Authorization'] = 'Token '+token;
                    }else {
                        http.defaults.headers.common['Authorization'] = 'Token '+self.$route.query.token;
                    }
                    console.log(http.defaults.headers.common)
                    http.get('/statistic/heat').then(function(res){
                        if(res.status == 200) {
                            self.popularityList = res.data;
                            console.log(self.popularityList)
                        }else {
                            window.setupWebViewJavascriptBridge(function(bridge) {
                                bridge.callHandler('getToken', {'targetType':'0','targetId':'0'}, function responseCallback(responseData) {
                                    self.getPopularity(responseData.token);
                                })
                            })
                        }
                    }).catch(function(err){
                        self.idx2++;
                        window.setupWebViewJavascriptBridge(function(bridge) {
                            bridge.callHandler('getToken', {'targetType':'0','targetId':'0'}, function responseCallback(responseData) {
                                self.getPopularity(responseData.token);
                            })
                        })
                    });
                }
            },
            deleteItem: function(index) {
                window.setupWebViewJavascriptBridge(function(bridge) {
                    let _lan = (navigator.browserLanguage || navigator.language).toLowerCase();
                     if(_lan === 'zh-cn') {
                        bridge.callHandler('makeToast', '');
                     }else {
                        bridge.callHandler('makeToast', '');
                     }
                     http.get('/groupyuser/report',{
                        params: {
                            reportedUserId:index
                        }
                     }).then(function(res){
                        console.log('');
                     })
                })
            }
        },
        computed: {
            swiper() {
                return this.$refs.mySwiper.swiper
            }
        },
        created() {
            var self = this;
            let _lan = (navigator.browserLanguage || navigator.language).toLowerCase();
             if(_lan === 'zh-cn') {
                 self.fans_text= {
                    mine: '',
                    add: '',
                    Gcoin: 'G',
                    like: '',
                    time: '',
                    noneGcoin: 'G',
                    noneLike: '',
                    noneComment: ''

                }
              } else {
                self.fans_text= {
                    mine: '',
                    add: '',
                    Gcoin: '',
                    like: 'Like',
                    time: '',
                    noneGcoin: '',
                    noneLike: 'Like',
                    noneComment: ''
                }
              }
            self.getGcoin();
            self.getPopularity();
            self.getJoin();

        }
    }
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
  .header {
    .income {
        text-align: left;
    }
  }
  .content {
    position: absolute;
    top: 144.5px;
    left: 0;
    width: 100%;
    height: calc(100vh - 143.5px);
    .swiper-slide {
        height: calc(100vh - 143.5px);
    }
  }
   .left {
    opacity: 0.2;
    transition: opacity 0.3s;
 }
.left_show {
    opacity: 1 !important;
}
</style>



